                </div>
                <div class="nav-one fl" @click="toggle({name:'Top'})">
                    <img src="../../assets/images/top.png" alt="" class="auto-img">
                    <div class="one-zi">榜单</div>
                </div>
                <div class="nav-one fl" >
                    <img src="../../assets/images/free.png" alt="" class="auto-img">
                    <div class="one-zi">免费</div>
                </div>
                <div class="nav-one fl">
                    <img src="../../assets/images/end.png" alt="" class="auto-img" @click="toggle({name:'Topdetail',query:{_id:'564eb878efe5b8e745508fde',title:'完结榜'}})">
                    <div class="one-zi">完结</div>
                </div>
                <div class="nav-one fl">
                    <img src="../../assets/images/vip.png" alt="" class="auto-img">
                    <div class="one-zi">会员</div>
                </div>
            </div>
            <div class="find-importan clearfix" @click="toggle({name:'Bookdetail',query:{id:'592fe687c60e3c4926b040ca'}})">
                <div class=" img fl"><img src="../../assets/images/impt.png" alt="" class="auto-img"></div>
                <span class="fl">废物少年,一人一剑掀翻诸天!</span>
                <div class="img-big fr"><img src="../../assets/images/big.png" alt="" class="auto-img"></div>
            </div>
            <div class="today-see">
                <div class="see-title clearfix">
                    <div class="fl">
                        今日必读
                    </div>
                    <div class="fr" @click="toggle({name:'Topdetail',query:{_id:'54d42d92321052167dfb75e3'}})">
                        查看更多
                    </div>
                </div>
                <div class="see-boss clearfix" @click="toggle({name:'Bookdetail',query:{id:'5e27abf72c71ba233dd8e63d'}})">
                    <div class="fl img-size">
                        <img src="../../assets/images/sanguo.png" alt="" class="auto-img">
                    </div>
                    <div class="fl img-msg" >
                        <div>三国之曹家逆子</div>
                        <div class="img-text">许褚：“主公，少主又说他病了！”许褚：“主公，少主又闯祸了！”许褚：“主公，少主又不见了！”曹操猛拍桌子，铁青着脸吼道：“我曹孟德英明一世，怎么就生了这么个逆子？”许褚：“主公，少主把吕布杀了！”许褚：“少主把邺城攻下了！”曹操仰天大笑：</div>
                        <div>
                            <div class="fl">你我当初</div>
                            <div class="fr">历史</div>
                            <div class="fr">连载</div>
                        </div>
                    </div>
                </div>
                <div class="see-list clearfix">
                    <div class="see-list-one fl" v-for="(item,index) in seeList" :key="index" @click="toggle({name:'Bookdetail',query:{id:item.id}})">
