                        <div class=" one-text">{{item.author}}</div>
                    </div>
                </div>
            </div>
            <div class="find-list">
                <div class="find-list-one" v-for="(item,index) in listTitle" :key="index">
                    <div class="see-title clearfix">
                        <div class="fl">
                            {{item.title}}
                        </div>
                        <div class="fr" @click="tougle(index)">
                            换一换
                            <span >
                                <svg t="1597024057598" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2117" width="16" height="16"><path d="M946.962 627.613a48.888 48.888 0 0 0-45.255 29.399 429.42 429.42 0 0 1-794.095 3.633V644.13a49.548 49.548 0 0 0-49.549-49.548 49.218 49.218 0 0 0-49.548 37.656v243.118a49.548 49.548 0 0 0 99.097 0v-31.38a528.516 528.516 0 0 0 885.595-150.298 48.888 48.888 0 0 0-46.245-66.064z m68.376-280.774V148.645a49.548 49.548 0 0 0-99.096 0v33.032A528.516 528.516 0 0 0 30.316 330.323a49.218 49.218 0 0 0 46.576 66.064 47.897 47.897 0 0 0 44.924-29.399 429.42 429.42 0 0 1 794.426-3.633v16.186a49.879 49.879 0 0 0 99.096 12.222c0.33-18.829 0-44.924 0-44.924z" p-id="2118"></path></svg>
                            </span>
                            
                        </div>
                    </div>
                    <div class="textlist">
                        <div class="textlist-one clearfix" v-for="(itm,i) in item.listType" :key="i" @click="toggle({name:'Bookdetail',query:{id:itm._id}})">
                            <div class="textlist-img fl">
                                <img :src="itm.cover" alt="" class="auto-img">
                            </div>
                            <div class="textlist-msg fl">
                                <div>{{itm.title}}</div>
                                <div class="img-text">{{itm.shortIntro}}</div>
                                <div class="clearfix">
                                    <div class="fl">{{itm.author}}</div>
                                    <div class="fr">{{itm.majorCate}}</div>
                                    <div class="fr">连载</div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </van-pull-refresh>
</template>

<script>
    export default {
        data(){
            return{
                isLoading: false,
                num:[],
                bannerList:[
                    {
                        url:require("../../assets/images/banner1.jpg"),
                        id:"5aa1063fdb70a41e4ae319d2"
                    },
                    {
                        url:require("../../assets/images/banner2.jpg"),
                        id:"5b33575b0983ce2c2d5d4e5b"
                    },
                    {
                        url:require("../../assets/images/banner3.jpg"),
                        id:"59510fc4eeb91c460802c813"
                    },
                    {
                        url:require("../../assets/images/banner4.jpg"),
                        id:"574e3b75d5d9e7671a30e84c"
                    },
                ],
                seeList:[
                    {
                        title:"噬天至尊",
                        author:"轻弹烟灰",
                        url:require("../../assets/images/tianzun.png"),
                        id:"5e5363c60883173aad82ce6b"
                    },
                    {
                        title:"遮天记",
                        author:"归来的洛秋",
                        url:require("../../assets/images/zhetian.png"),
                        id:"5a9fb71596a7481e45100a51"
                    },
                    {
                        title:"南宋第一卧底",
                        author:"龙渊",
                        url:require("../../assets/images/diyi.png"),
                        id:"5c6fcfa760f0ff2e796323dc"
                    },
                    {
                        title:"鬼医神农",
                        author:"三尺神剑",
                        url:require("../../assets/images/xiaonvhai.png"),
                        id:"59510fc4eeb91c460802c813"
                    },
                ],
                value:"",
                titleClas:[
                    {
                        title:"男生",
                        isActive:true
                    },
                    {
                        title:"女生",
                        isActive:false
                    },
                    {
                        title:"女生",
                        isActive:false
                    },
                    {
                        title:"女生",
                        isActive:false
                    }
                ],
                searchData:[],
                listData:[],
                smalllistData:[],
                books:[],
                listTitle:[
                    {
                        title:"为你推荐",
                        listType:[],
                        book:[]
                    }
                ]
            }
        },
        created(){
            
            this.getTop('54d42d92321052167dfb75e3');
        },
        
        methods:{ 
            
            // 获取书籍列表  
            getTop(id){
                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0
                });
                this.axios({
                    method: "GET",
                    url: "/rank/"+id,
                    
                }).then(result => {
                    this.$toast.clear(); 
                    
                    for(let i = 0 ; i < result.data.ranking.books.length ; i++){
                        result.data.ranking.books[i].cover = this.imgKey(result.data.ranking.books[i].cover)
                    }
                    this.books = result.data.ranking.books
                    this.threeNum() 
                })
                .catch(err => {
                    this.$toast.clear();
                    
                });
            },
            // 获取三个随机数
            threeNum(index){
                
                if(this.num.length == 3){
                    if(index||index==0){
                        // 
                        for(let i = 0 ; i < this.num.length ; i ++){
                            this.listTitle[index].listType.push(this.books[this.num[i]])
                        }
                    }else{
                        for(let j = 0 ; j < this.listTitle.length ; j++){
                            for(let i = 0 ; i < this.num.length ; i ++){
                                this.listTitle[j].listType.push(this.books[this.num[i]])
                            }
                        }
                    }
                    
                    
                }else{
                    let a = this.getNum(0,21)
                    if(this.num.indexOf(a) == -1){
                        this.num.push(a)
                    }
                    this.threeNum(index)
                }
            },
            // 下拉刷新
            onRefresh() {
                setTimeout(() => {
                    this.isLoading = false;
                }, 1000);
            },
            // 换一换。切换书籍
            tougle(index){
                this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                });
                this.num = [],
                this.listTitle[index].listType = []
                this.threeNum(index);
                setTimeout(()=>{
                    this.$toast.clear();
                },500) 
            },
            // 随机数封装
            getNum(min,max){
                return Math.floor(Math.random()*(max-min+1)+min); 
            },
            // 路由切换
            toggle(e){
                this.$router.push(e)
            },

            toggleTitclas(item){
                for( let i = 0 ; i < this.titleClas.length ; i ++ ){
                    this.titleClas[i].isActive = false
                }
                item.isActive = true
            },
            search(){
                this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "/auto-complete",
                        params: {
                            query:this.value
                        }
                    }).then(result => {
                        this.$toast.clear(); 
                        
                        this.searchData = result.data.keywords

                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
            },
            imgKey(value) {
                return unescape(value).slice(7);
            },
            goa(item){
                this.$toast.loading({
                        message: "加载中...",
                        forbidClick: true,
                        duration: 0
                    });
                    this.axios({
                        method: "GET",
                        url: "search?keyword="+item,
                        params: {
                            
                        }
                    }).then(result => {
                        this.$toast.clear();                     
                        let id = result.data.books[0]._id
                        this.$router.push({name:"Bookdetail",query:{id}})
                    })
                    .catch(err => {
                        this.$toast.clear();
                        
                    });
            },
            
        }
    }
</script>

